import './App.css';
import {
    BrowserRouter as Router,
    Routes,
    Route,
} from "react-router-dom";
import Diagram from './Diagram';
import Home from './Home';
import myVideo from "./TheScene.mp4";
import Ilm from "./Ilm";

import { MathJax, MathJaxContext } from "better-react-mathjax";

function TDuality() {

  return (
    <div class='main'> 
        <p >The <Ilm>S^1</Ilm> sigma model in 1+1 dimensions is defined by the action

        <MathJax>{"$$S[\\beta,\\phi]=\\frac{\\beta^2}{2}\\int_M |\\textrm{d}\\phi|^2$$"}</MathJax>
        
        where <Ilm>\\phi</Ilm> is valued in <Ilm>U(1)\\cong\\mathbb R/\\mathbb Z</Ilm> and  <Ilm>\\beta</Ilm> is a real coupling constant.
         We can add an extra field <Ilm>\\theta</Ilm> to the action:


        <MathJax>{"$$S[\\beta,\\phi,\\theta]=\\int_M \\frac{\\beta^2}{2}|\\textrm{d}\\phi|^2+\\textrm{d}\\phi\\wedge \\textrm{d}\\theta$$"}</MathJax>
        
        The new term is linear in <Ilm>\\theta</Ilm>, and therefore does not alter the partition function from the original expression. We can complete the
        square to obtain
        
        
        <MathJax>{"$$S[\\beta,\\phi,\\theta]= \\int_M \\frac{\\beta^2}{2}\\Big|\\textrm{d}\\phi -\\star\\frac{\\textrm{d} \\theta}{\\beta^2}\\Big|^2+\\frac1{2\\beta^2} \\big|\\textrm{d}\\theta\\big|^2$$"}</MathJax>
        
        This theory exhibits a T-duality,
        given by sending 

        <MathJax>{"$$\\textrm{d}\\phi=\\frac{\\star\\textrm{d}\\theta}{\\beta^2}$$"}</MathJax>

        In 1+1 dimensions, the Hodge star simply swaps the two cartesian components of
        any 1-form: <Ilm>\\star\\textrm dt=\\textrm dx</Ilm> and <Ilm>\\star\\textrm dx=\\textrm dt</Ilm>. This gives some nice intuition for the duality
        in the classical theory. The classical equation of motion <Ilm>\\textrm d\\star\\textrm d\\phi=0</Ilm> admits wave-equation solutions of the form
        
        <MathJax>{"$$\\phi(x,t)=\\phi_+(x+t)+\\phi_-(x-t)$$"}</MathJax>

        and the duality is satisfied by simply negating the second term:

        <MathJax>{"$$\\theta(x,t)=\\phi_+(x+t)-\\phi_-(x-t)$$"}</MathJax>

        Below is an example of the duality sending <Ilm>\\phi(x,t)=x-t\\leftrightarrow\\theta(x,t)=t-x</Ilm>. The duality exchanges a field that 
        rotates clockwise over time to one that rotates counterclockwise.

        </p>
        <div style={{textAlign: 'center'}}>
          <video width="400" height="400" autoPlay loop muted>
            <source src={myVideo} type="video/mp4"/>
          Your browser does not support the video tag.
          </video>
        </div>

        <p>
          Unfortunately, in the quantum theory we need to consider all field configurations, including ones that violate the classical equations. In this case
          we don't have such a nice interpretation; the only way to compute <Ilm>\\theta</Ilm> given <Ilm>\\phi</Ilm> is by direct integration.
        </p>

        <p>
          Another claim about the <Ilm>S^1</Ilm> sigma model that I found confusing was the argument that it has a global <Ilm>U(1)\\times U(1)\\ltimes \\mathbb Z_2</Ilm> symmetry.
        </p>



    </div>
  );
}

export default TDuality;
