import './App.css';
import { useEffect, useRef, useState } from "react";
import CanvasView from './CanvasView';
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css'
import { AwesomeButton } from "react-awesome-button";
import 'react-awesome-button/dist/themes/theme-blue.css';
import { MathJax } from 'better-react-mathjax';


function Diagram() {

  const [velocity, setVelocity] = useState(0);
  let [points, setPoints] = useState([]);
  let [lines, setLines] = useState([]);
  let [hyperbolas, setHyperbolas] = useState(false);

  const changeVelocity = (v) => {
    setVelocity(v / 400);
  }

  const timeDilation = () => {
    points.push([0, 0]);
    points.push([0, 1]);
    points.push([0, 2]);
    points.push([0, 3]);
    points.push([0, 4]);
    points.push([0, 5]);
    points.push([0.5, 1]);
    points.push([1, 2]);
    points.push([1.5, 3]);
    points.push([2, 4]);
    points.push([2.5, 5]);
    setPoints(points)
    setLines(lines)
  }

  const lengthContraction = () => {
    lines.push([0, -10, 0, 10]);
    lines.push([1, -10, 1, 10]);
    setPoints(points)
    setLines(lines)
  }

  const twinParadox = () => {
    lines.push([0, 0, 1, 2]);
    lines.push([1, 2, 0, 4]);
    lines.push([0, 0, 0, 4]);
    points.push([0,2])
    setPoints(points)
    setLines(lines)
  }

  const ladderParadox = () => {
    lines.push([0, 0, 0, 10]);
    lines.push([1, 0, 1, 10]);
    lines.push([1, 0, -10, 18]);
    lines.push([2, 0, -9, 18]);
    setLines(lines)
  }

  const clear = () => {
    setPoints([])
    setLines([])
    setHyperbolas(false)
  }

  return (
    <div className="Diagram" style={{width: 1000, position: 'relative', left: '50%', transform: 'translate(-50%)',  backgroundColor: '#ddd',
    color: '#111333', width: '1000px', marginTop: '50px', padding: '25px'}}>
  
      <br/>
      <p>For a while I struggled to visualize Lorentz transformations and get an
         intuitive understanding of how they lead to time dilation, length contraction, and all the weird paradoxes. During my freshman year I made this 
         little web app to help myself understand them better, so maybe others will find it helpful too.</p>

         <p>The graph shows the (x,t) plane in Minkowski space. Click on it to add points. Click and drag to draw line segments. Adjusting 
           the slider will boost the drawn points and lines to a frame with
           a different velocity. The buttons give some preset visualizations that I personally found to be helpful.
        </p>

      <br/>
      <div style={{position: 'relative', width: '70%', textAlign: 'center', left: '50%',  transform: 'translate(-50%)'}}>
        <CanvasView width={600} height={600} velocity={velocity} points={points} lines={lines} hyperbolas={hyperbolas}/>
        <div style={{position: 'relative', left: '50%', transform: 'translate(-50%)', width: 600}}>
          <Slider
              min={-400}
              max={400}
              value={velocity * 400}
              onChangeStart={() => {}}
              onChange={changeVelocity}
              onChangeComplete={() => {}}
              className='vSlider'
              format={value => value / 400 + 'c'}
            />
        </div>
        <p style={{position: 'relative', left: '50%', transform: 'translate(-50%)', fontFamily: 'Times', fontStyle: 'italic', fontSize: '16pt', textAlign: 'center'}}>
        <MathJax inline={true}>{"\\(v=" + velocity.toFixed(3) + "c\\)"}</MathJax></p>
        <p style={{position: 'relative', left: '50%', transform: 'translate(-50%)', fontFamily: 'Times', fontStyle: 'italic', fontSize: '16pt', textAlign: 'center'}}>
        <MathJax inline={true}>{"\\(\\gamma=" + (1/Math.sqrt(1-velocity*velocity)).toFixed(3) + "\\)"}</MathJax>
        </p>
      </div>
      <div style={{float: 'left', width: '29%'}}>
        <h4>Presets</h4>
        <div >
          <AwesomeButton onPress={timeDilation} style={{width: 180}}>
            Time Dilation
          </AwesomeButton>
          <AwesomeButton onPress={lengthContraction} style={{width: 180}}>
            Length Contraction
          </AwesomeButton>
          <br/>
          <br/>
          <AwesomeButton onPress={twinParadox} style={{width: 180}}>
            Twin Paradox
          </AwesomeButton>
          <br/>
          <br/>
          <AwesomeButton onPress={ladderParadox} style={{width: 180}}>
            Ladder Paradox
          </AwesomeButton>
          <br/>
          <br/>
          <AwesomeButton onPress={() => setHyperbolas(true)} style={{width: 180}}>
            Hyperbolas
          </AwesomeButton>
          <br/>
          <br/>
          <AwesomeButton onPress={clear} style={{width: 180}}>
            Clear
          </AwesomeButton>
        </div>

      
      </div>


      <h3>Hyperbolas</h3>
        <p>The curves given by () are left unchanged by Lorentz transformations. In geometry jargon, these hyperbolas are the flows of the
           Killing field of <MathJax inline={true}>{"\\(\\mathbb R^{1,1}\\)"}</MathJax>.</p>

    </div>
  );
}

export default Diagram;
