import { LinearProgress } from "@material-ui/core";
import { useEffect, useRef, useState } from "react";

function CanvasView(props) {

  const canvasRef = useRef(null);

  const WIDTH = props.width
  const HEIGHT = props.height
  let VELOCITY = props.velocity

  let points = props.points;
  let lines = props.lines;

  let [isDrawing, setIsDrawing] = useState(false);
  let [startX, setStartX] = useState(null);
  let [startY, setStartY] = useState(null);
  let [mouseX, setMouseX] = useState(null);
  let [mouseY, setMouseY] = useState(null);

  const SxToScreenx = (x) => {
    return 80 * x + WIDTH / 2
  } 

  const StToScreeny = (t) => {
    return HEIGHT / 2 - 80 * t + 160
  } 

  const LToSx = (x, t) => {
    return (x - VELOCITY * t) / (Math.sqrt(1 - Math.pow(VELOCITY, 2)))
  }

  const LToSt = (x, t) => {
    return (t - VELOCITY * x) / (Math.sqrt(1 - Math.pow(VELOCITY, 2)))
  }

  const LToScreenx = (x, t) => {
    return SxToScreenx(LToSx(x, t))
  }

  const LToScreeny = (x, t) => {
    return StToScreeny(LToSt(x, t))
  }

  const ScreenxToSx = (x) => { 
    return (x - WIDTH / 2) / 80
  }

  const ScreenyToSt = (y) => {
    return (HEIGHT / 2 - y + 160) / 80
  }

  const SToLx = (x, t) => {
    return (x + VELOCITY * t) / (Math.sqrt(1 - Math.pow(VELOCITY, 2)))
  }

  const SToLt = (x, t) => {
    return (t + VELOCITY * x) / (Math.sqrt(1 - Math.pow(VELOCITY, 2)))
  }

  const ScreenToLx = (x, y) => {
    return SToLx(ScreenxToSx(x), ScreenyToSt(y))
  }

  const ScreenToLt = (x, y) => {
    return SToLt(ScreenxToSx(x), ScreenyToSt(y))
  }

  let onMouseDown = (e) => {
    setIsDrawing(true);
    setStartX(e.clientX - e.target.getBoundingClientRect().left);
    setStartY(e.clientY - e.target.getBoundingClientRect().top);
  }

  let onMouseUp = (e) => {
      setIsDrawing(false);

      if (Math.pow(mouseX - startX, 2) + Math.pow(mouseY - startY, 2) > 25) {
        lines.push([ScreenToLx(startX, startY), ScreenToLt(startX, startY), ScreenToLx(mouseX, mouseY), ScreenToLt(mouseX, mouseY)]);
      } else {
        points.push([ScreenToLx(mouseX, mouseY), ScreenToLt(mouseX, mouseY)]);
      }

      setStartX(null);
      setStartY(null);
  }

  let onMouseMove = (e) => {
      setMouseX(e.clientX - e.target.getBoundingClientRect().left);
      setMouseY(e.clientY - e.target.getBoundingClientRect().top);
  }

  useEffect(() => {

    const canvas = canvasRef.current
    const ctx = canvas.getContext('2d')
    ctx.restore()
    ctx.save()
    ctx.translate(0.5, 0.5);
    
    ctx.clearRect(0, 0, WIDTH, HEIGHT)
  

    // Black background
    ctx.fillStyle = '#000000'
    ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height)

    // Gridlines
    ctx.strokeStyle = '#444444'
    for (let x = -5; x <= 5; x += 1) {
      ctx.beginPath();
      ctx.moveTo(SxToScreenx(x), StToScreeny(-5));
      ctx.lineTo(SxToScreenx(x), StToScreeny(7));
      ctx.stroke()
      ctx.closePath();
    }
    for (let t = -5; t <= 7; t += 1) {
      ctx.beginPath();
      ctx.moveTo(SxToScreenx(-5), StToScreeny(t));
      ctx.lineTo(SxToScreenx(5), StToScreeny(t));
      ctx.stroke()
      ctx.closePath();
    }

    ctx.strokeStyle = '#FFFFFF'
    for (let x = -5; x <= 5; x += 1) {
      if (x == 0) {
        ctx.lineWidth = 5;
      } else {
        ctx.lineWidth = 2;
      }
      ctx.beginPath();
      ctx.moveTo(LToScreenx(x, -5), LToScreeny(x, -5));
      ctx.lineTo(LToScreenx(x, 7), LToScreeny(x, 7));
      ctx.stroke()
      ctx.closePath();
    }
    for (let t = -5; t <= 7; t += 1) {
      if (t == 0) {
        ctx.lineWidth = 5;
      } else {
        ctx.lineWidth = 2;
      }
      ctx.beginPath();
      ctx.moveTo(LToScreenx(-5, t), LToScreeny(-5, t));
      ctx.lineTo(LToScreenx(5, t), LToScreeny(5, t));
      ctx.stroke()
      ctx.closePath();
    }


    // Hyperbolas
    if (props.hyperbolas) {
      ctx.strokeStyle = '#AAAAAA'
      for (let l in [0,1,2,3]) {
        let k = [1,5,12,20][l];
        for (let x = -5; x <= 4.9; x += 0.1) {
          ctx.beginPath();
          ctx.moveTo(LToScreenx(x, Math.sqrt(x*x + k)), LToScreeny(x, Math.sqrt(x*x + k)));
          ctx.lineTo(LToScreenx(x + 0.1, Math.sqrt((x+0.1)*(x+0.1) + k)), LToScreeny(x + 0.1, Math.sqrt((x+0.1)*(x+0.1) + k)));
          ctx.moveTo(LToScreenx(x, -Math.sqrt(x*x + k)), LToScreeny(x, -Math.sqrt(x*x + k)));
          ctx.lineTo(LToScreenx(x + 0.1, -Math.sqrt((x+0.1)*(x+0.1) + k)), LToScreeny(x + 0.1, -Math.sqrt((x+0.1)*(x+0.1) + k)));
          ctx.stroke();
          ctx.closePath();
        }
      }
    }


    // Custom points
    ctx.fillStyle = '#FF0000'
    ctx.lineWidth = 1
    ctx.strokeStyle = '#660000'
    for (let i in points) {
      let point = points[i]
      ctx.beginPath();
      ctx.arc(LToScreenx(point[0], point[1]), LToScreeny(point[0], point[1]), 5, 0, 2 * Math.PI);
      ctx.fill();
      ctx.stroke();
    }

    // Custom Lines
    ctx.strokeStyle = '#FF0000'
    ctx.lineWidth = 2
    for (let i in lines) {
      let [x1, y1, x2, y2] = lines[i]
      ctx.beginPath();
      ctx.moveTo(LToScreenx(x1, y1), LToScreeny(x1, y1));
      ctx.lineTo(LToScreenx(x2, y2), LToScreeny(x2, y2));
      ctx.stroke();
      ctx.closePath();
    }

    if (isDrawing) {
      ctx.beginPath();
      ctx.moveTo(startX, startY);
      ctx.lineTo(mouseX, mouseY);
      ctx.stroke();
      ctx.closePath();
    }
    
  }, [HEIGHT, WIDTH, VELOCITY, startX, startY, mouseX, mouseY, points, lines])

  return (
    <div className="CanvasView" width="100%" height="100%">
      <canvas ref={canvasRef} width={WIDTH} height={HEIGHT} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseMove={onMouseMove} style={{cursor: 'pointer'}}>

      </canvas>
    </div>
  );
}

export default CanvasView;
